import { fetchEndpoint } from '../server/network'

// 
export const activityList = () =>
	fetchEndpoint('/v2/ecapi.activity.list', 'POST', {})

// 
export const activityDetail = id =>
	fetchEndpoint('/v2/ecapi.activity.get', 'POST', {
		activity: id //id
	})



